.checkboxItem {
  align-items: center;
}
.checkboxItem:global(.carbon) {
  padding-left: 12px;
  padding-right: 12px;
  border-color: transparent;
  font-size: inherit;
}
.checkboxItem:global(.carbon) .slotAfter {
  -webkit-margin-start: 12px;
  -webkit-margin-end: 0;
}
.checkboxItem:global(.carbon) .slotBefore {
  -webkit-margin-start: 0;
  -webkit-margin-end: 12px;
}
.checkboxItem:global(.carbon):global(.spottable):focus {
  color: black;
  background-color: var(--agate-highlight-color, #6abe0b);
  border-color: transparent;
}
