  background-image: linear-gradient(to top, transparent, rgba(0, 0, 0, 0.05));
  box-shadow: none;
}
.button:global(.titanium).vertical.increment:global(.spottable):focus .bg {
  background-color: var(--agate-highlight-color, #2a48ca);
  background-image: linear-gradient(to bottom, transparent, rgba(255, 255, 255, 0.15) 14%, transparent);
}
