.toggleIcon:global(.carbon) {
  width: 46px;
  height: 46px;
  border: inherit solid inherit;
  border-radius: inherit;
  color: #000;
  background-color: var(--agate-accent-color, #8fd43a);
}
.toggleIcon:global(.carbon) .icon {
  font-size: inherit;
  visibility: hidden;
  margin: 0;
  line-height: 46px;
  width: 46px;
  height: 46px;
}
.toggleIcon:global(.carbon).selected {
  border-color: inherit;
}
.toggleIcon:global(.carbon).selected .icon {
  visibility: visible;
}
