.skinFrame {
  padding: 1em;
  position: relative;
}
.skinFrame:global(.carbon) {
  background-color: black;
  background-image: none;
}
.skinFrame:global(.carbon)::before {
  content: "";
  display: block;
  background-image: none;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.skinFrame:global(.cobalt) {
  background-color: #ededed;
  background-image: radial-gradient(hsla(var(--agate-accent-h, 0), var(--agate-accent-s, 0), var(--agate-accent-l, 0), 0.2), transparent 60%), linear-gradient(to bottom, #ededed 43%, #fff 90%);
}
.skinFrame:global(.cobalt)::before {
  content: "";
  display: block;
  background-image: none;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.skinFrame:global(.cobalt):global(.night) {
  background-color: #44559f;
  background-image: radial-gradient(hsla(var(--agate-accent-h, 0), var(--agate-accent-s, 0%), var(--agate-accent-l, 0%), 0.2), transparent 60%), linear-gradient(to bottom, #44559f 43%, #44559f 90%);
}
.skinFrame:global(.cobalt):global(.night)::before {
