.colorPicker {
  display: inline-block;
  vertical-align: inherit;
}
.colorPicker .transitionContainer {
  position: absolute;
  display: inline-block;
  z-index: 1;
  pointer-events: none;
  min-width: 312px;
  width: 100%;
}
.colorPicker .transitionContainer.openTransitionContainer {
  pointer-events: inherit;
}
.colorPicker .group {
  display: block;
}
.colorPicker .sliders {
  display: none;
}
.colorPicker.extended .sliders {
  display: block;
}
.colorPicker:global(.carbon) .palette {
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 84px;
  box-shadow: none;
  margin: 0;
}
.colorPicker:global(.carbon) .palette .swatch {
  margin: 12px;
}
.colorPicker:global(.carbon) .palette .sliders {
  padding: 12px 12px 24px 12px;
}
