.progressBar:global(.carbon) {
  position: relative;
  height: 6px;
  width: 100%;
  background-color: rgba(102, 102, 102, 0.5);
  background-image: linear-gradient(to top, transparent, rgba(0, 0, 0, 0.03));
  border-radius: 0;
  transform: none;
}
.progressBar:global(.carbon).small {
  height: 3px;
}
.progressBar:global(.carbon).vertical {
  height: 100%;
  width: 6px;
  transform: none;
}
.progressBar:global(.carbon).vertical.small {
  width: 3px;
}
.progressBar:global(.carbon).vertical .fill {
  background-image: linear-gradient(to top, transparent, rgba(255, 255, 255, 0.75));
}
.progressBar:global(.carbon) .fill {
  background-color: var(--agate-highlight-color, #6abe0b);
  background-image: linear-gradient(to right, transparent, rgba(255, 255, 255, 0.75));
  border-radius: 0;
}
