.sliderButton {
  --sliderbutton-item-count: 0;
  --sliderbutton-item-size: calc(100% / var(--sliderbutton-item-count));
  padding: 0 calc(var(--sliderbutton-item-size) / 2);
  box-sizing: border-box;
}
.sliderButton::before {
  content: "";
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  display: none;
}
.sliderButton.pressed::before {
  display: block;
}
.sliderButton .knob {
  position: absolute;
  top: 0;
  height: 100%;
  width: var(--sliderbutton-item-size);
  left: calc(var(--knob-value) * var(--sliderbutton-item-size));
  will-change: left;
  transition: left 200ms ease-in-out;
  box-sizing: border-box;
}
.sliderButton .track {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  text-align: center;
}
.sliderButton:global(.carbon) {
  margin: 0 12px;
  height: 84px;
  border: 0 solid transparent;
  border-radius: 999px;
  background-color: rgba(102, 102, 102, 0.5);
  background-image: linear-gradient(to top, transparent, rgba(0, 0, 0, 0.03));
  background-clip: initial;
  cursor: pointer;
  transform: none;
}
.sliderButton:global(.carbon) .client {
  transform: none;
  color: var(--agate-accent-color, #8fd43a);
}
.sliderButton:global(.carbon) .knob {
  background-color: var(--agate-accent-color, #8fd43a);
  background-image: linear-gradient(to bottom, transparent, rgba(51, 51, 51, 0.5));
  box-shadow: 0 3px 9px 3px rgba(0, 0, 0, 0.3);
  border: 0 solid transparent;
  border-radius: 999px;
  transform: none;
}
.sliderButton:global(.carbon):global(.spottable):focus {
  color: black;
  background-color: rgba(102, 102, 102, 0.5);
  background-image: linear-gradient(to bottom, hsla(var(--agate-accent-h, 0), var(--agate-accent-s, 0), var(--agate-accent-l, 0), 0.5), var(--agate-accent-color, #8fd43a));
}
.sliderButton:global(.carbon):global(.spottable):focus .knob {
  box-shadow: 0 3px 9px 3px rgba(0, 0, 0, 0.3);
}
