.dateTimePicker {
  position: relative;
}
.dateTimePicker::before {
  content: "";
  position: absolute;
  width: 95%;
  left: 50%;
  transform: translateX(-50%);
}
:global(.carbon) .dateTimePicker {
  font-size: 24px;
  width: 699px;
}
:global(.carbon) .dateTimePicker::before {
  height: 84px;
  background-image: inherit;
  background-color: inherit;
}
