@keyframes searching-spinner {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0.1;
  }
}
@keyframes loading-spinner {
  0% {
    transform: translateY(-50%) translateY(100%);
  }
  50% {
    transform: translateY(-50%) translateY(-100%);
  }
  100% {
    transform: translateY(-50%) translateY(100%);
  }
}
.spinner {
  line-height: 0;
}
.spinner:global(.carbon) .bg {
  display: inline-block;
  width: 52px;
  height: 52px;
  position: relative;
  white-space: nowrap;
  margin: inherit;
  padding: 9px;
}
.spinner:global(.carbon).huge .bg {
  width: 132px;
  height: 132px;
  margin: inherit;
  padding: 21px;
}
.spinner:global(.carbon).small .bg {
  width: 36px;
  height: 36px;
  margin: inherit;
  padding: 5px;
}
.spinner:global(.carbon).smallest .bg {
  width: 21px;
  height: 21px;
  margin: inherit;
  padding: 3px;
}
.spinner:global(.carbon).dark .node {
  background-color: #262626;
}
.spinner:global(.carbon).light .node {
  background-color: #fff;
}
.spinner:global(.carbon).searching .node {
  position: absolute;
  will-change: opacity;
  transform-origin: center bottom;
  animation: searching-spinner 1.2s linear infinite;
  top: 50%;
  left: 50%;
  width: 3px;
  height: 12px;
  border-radius: 50px;
}
.spinner:global(.carbon).searching .node12 {
  transform: translate(-50%, -100%) rotate(-360deg) translateY(-100%);
  animation-delay: -1.3s;
}
.spinner:global(.carbon).searching .node11 {
  transform: translate(-50%, -100%) rotate(-330deg) translateY(-100%);
  animation-delay: -1.2s;
}
.spinner:global(.carbon).searching .node10 {
  transform: translate(-50%, -100%) rotate(-300deg) translateY(-100%);
  animation-delay: -1.1s;
}
.spinner:global(.carbon).searching .node9 {
  transform: translate(-50%, -100%) rotate(-270deg) translateY(-100%);
  animation-delay: -1s;
}
.spinner:global(.carbon).searching .node8 {
  transform: translate(-50%, -100%) rotate(-240deg) translateY(-100%);
  animation-delay: -0.9s;
}
.spinner:global(.carbon).searching .node7 {
  transform: translate(-50%, -100%) rotate(-210deg) translateY(-100%);
  animation-delay: -0.8s;
}
.spinner:global(.carbon).searching .node6 {
  transform: translate(-50%, -100%) rotate(-180deg) translateY(-100%);
  animation-delay: -0.7s;
}
.spinner:global(.carbon).searching .node5 {
  transform: translate(-50%, -100%) rotate(-150deg) translateY(-100%);
  animation-delay: -0.6s;
}
.spinner:global(.carbon).searching .node4 {
  transform: translate(-50%, -100%) rotate(-120deg) translateY(-100%);
  animation-delay: -0.5s;
}
.spinner:global(.carbon).searching .node3 {
  transform: translate(-50%, -100%) rotate(-90deg) translateY(-100%);
  animation-delay: -0.4s;
}
.spinner:global(.carbon).searching .node2 {
  transform: translate(-50%, -100%) rotate(-60deg) translateY(-100%);
  animation-delay: -0.3s;
}
.spinner:global(.carbon).searching .node1 {
  transform: translate(-50%, -100%) rotate(-30deg) translateY(-100%);
  animation-delay: -0.2s;
}
.spinner:global(.carbon).searching.huge .node {
  width: 9px;
  height: 27px;
}
.spinner:global(.carbon).searching.small .node {
  width: 3px;
  height: 9px;
}
.spinner:global(.carbon).searching.smallest .node {
  width: 2px;
  height: 5px;
}
.spinner:global(.carbon).loading .node {
  position: relative;
  display: inline-block;
  will-change: transform;
  animation: loading-spinner 0.8s ease-in-out infinite;
  transform: translateY(-50%);
  top: 50%;
  vertical-align: top;
  width: 6px;
  height: 6px;
  border-radius: 999px;
  --node-index: 1;
  --node-width: 6px;
  --node-proportion-offset: calc((var(--node-index) - 1) / (4 - 1));
  --node-even-spacing: calc(var(--node-proportion-offset) * 100%);
  --node-relative-offset: calc((var(--node-index) - 1) * var(--node-width));
  left: calc(var(--node-even-spacing) - var(--node-relative-offset) - (var(--node-proportion-offset) * 6px));
}
.spinner:global(.carbon).loading .node:nth-child(4) {
  animation-delay: 0ms;
  --node-index: 4;
}
.spinner:global(.carbon).loading .node:nth-child(3) {
  animation-delay: -50ms;
  --node-index: 3;
}
.spinner:global(.carbon).loading .node:nth-child(2) {
  animation-delay: -100ms;
  --node-index: 2;
}
.spinner:global(.carbon).loading .node:nth-child(1) {
  animation-delay: -150ms;
  --node-index: 1;
}
.spinner:global(.carbon).loading.huge .node {
  width: 13.5px;
  height: 13.5px;
  --node-width: (13.5px);
}
.spinner:global(.carbon).loading.small .node {
  width: 4.5px;
  height: 4.5px;
  --node-width: 4.5px;
}
