.imageItem.horizontal .image {
  margin: auto 0;
}
.imageItem.captionOverlay .caption {
  overflow: hidden;
  position: absolute;
}
.imageItem:global(.carbon) {
  border: inherit solid inherit;
}
.imageItem:global(.carbon) .caption {
  height: 48px;
  line-height: 48px;
  text-align: center;
}
.imageItem:global(.carbon).captionOverlay .caption {
  width: inherit;
  height: inherit;
  color: inherit;
  opacity: inherit;
  padding: inherit;
  font-size: inherit;
  line-height: inherit;
  text-align: inherit;
  bottom: 6px;
}
.imageItem:global(.carbon):global(.spottable):focus::before {
  content: "";
  border-color: inherit;
  background-color: inherit;
}
