/* Styles for agate/Panels */
.panels,
.panels .viewport {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.panels .controls {
  position: absolute;
  z-index: 1;
}
.panels .controls > :first-child {
  -webkit-margin-start: 0;
}
.panels .controls > :last-child {
  -webkit-margin-end: 0;
}
.panels.breadcrumbPanels .breadcrumbs {
  position: absolute;
  top: 0;
  right: auto;
  bottom: auto;
  left: 0;
  z-index: 10;
  overflow: hidden;
}
.panels.breadcrumbPanels .breadcrumbs .breadcrumb {
  position: absolute;
  display: table;
}
.panels.breadcrumbPanels .breadcrumbs .breadcrumb .breadcrumbContent {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}
.panels.breadcrumbPanels[data-index="0"] .breadcrumbs {
  z-index: -1;
  pointer-events: none;
}
.panels:global(.carbon) {
  background-color: inherit;
}
.panels:global(.carbon) .controls {
  top: inherit;
  left: auto;
  right: inherit;
  padding-left: inherit;
  padding-right: initial;
}
:global(.enact-locale-right-to-left) .panels:global(.carbon) .controls {
  left: inherit;
  right: auto;
}
:global(.enact-locale-right-to-left) .panels:global(.carbon) .controls {
  padding-left: initial;
  padding-right: inherit;
}
.panels:global(.carbon) .partial {
  background-color: rgba(0, 0, 0, 0.5);
  background-image: inherit;
  height: 50%;
  width: auto;
}
.panels:global(.carbon).breadcrumbPanels .breadcrumbs {
  background-color: inherit;
  background-image: inherit;
  height: 100%;
  width: 100px;
}
.panels:global(.carbon).breadcrumbPanels .breadcrumbs .breadcrumb {
  height: 100%;
  width: 100px;
  font-family: "MuseoSans", sans-serif;
}
.panels:global(.carbon).breadcrumbPanels .viewport.transitioning {
  -webkit-clip-path: polygon(100px 0, 100% 0, 100% 100%, 100px 100%);
  clip-path: polygon(100px 0, 100% 0, 100% 100%, 100px 100%);
}
.panels:global(.carbon).breadcrumbPanels .viewport > :not([data-index="0"]) {
  width: calc(100% -  100px);
}
