.bodyText a:link,
.bodyText a:visited,
.bodyText a:hover,
.bodyText a:active {
  color: inherit;
  text-decoration: none;
}
:global(.enact-locale-right-to-left) .bodyText {
  text-align: right;
}
:global(.enact-locale-right-to-left) .bodyText.centered {
  text-align: center;
}
:global(.enact-locale-ja) .bodyText {
  line-break: strict;
}
