.panel {
  position: absolute;
  height: 100%;
  width: 100%;
  overflow: hidden;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}
.panel:global(.enact-fit) {
  height: auto;
}
:global(.enact-locale-right-to-left) .panel {
  direction: rtl;
}
.panel .body {
  position: relative;
  flex: 1;
  min-height: 0;
  opacity: 0;
  transition: opacity 200ms ease-out;
  will-change: opacity;
}
.panel .body.noHeader {
  padding-top: 0;
}
.panel .body.visible {
  opacity: 1;
}
.panel:global(.carbon) {
  padding: inherit;
}
.panel:global(.carbon) .header {
  padding: inherit;
}
.panel:global(.carbon) .body {
  padding: inherit;
}
