.thumbnailItem {
  align-items: center;
}
.thumbnailItem .thumbnail {
  vertical-align: middle;
}
.thumbnailItem .content {
  overflow: hidden;
}
.thumbnailItem .slotBefore {
  display: flex;
}
.thumbnailItem:global(.carbon) {
  line-height: inherit;
  height: inherit;
}
.thumbnailItem:global(.carbon) .thumbnail {
  height: inherit;
  width: inherit;
}
.thumbnailItem:global(.carbon).styled .thumbnail {
  border-radius: inherit;
  border: inherit solid inherit;
}
.thumbnailItem:global(.carbon).item .label {
  padding: 0 12px;
  font-size: inherit;
  color: inherit;
}
.thumbnailItem:global(.carbon) .content {
  padding: 0 12px;
  font-size: inherit;
}
.thumbnailItem:global(.carbon):global(.spottable):focus.item .label {
  color: inherit;
}
.thumbnailItem:global(.carbon).selected.item .label {
  color: inherit;
}
