.popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-sizing: border-box;
}
.popup.centered {
  text-align: center;
}
.popup.centered .buttons {
  text-align: center;
}
.popup.top {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  transform: none;
}
.popup .closeButton {
  position: absolute;
  top: 0;
}
:global(.enact-locale-right-to-left) .popup .closeButton {
  right: auto;
}
.popup:global(.carbon) {
  color: rgba(255, 255, 255, 0.75);
  min-width: 100%;
  max-width: 100%;
  box-shadow: none;
  background-color: rgba(0, 0, 0, 0.85);
  background-image: none;
  padding: 48px 12px;
  border: 1px solid transparent;
  border-width: 1px 0;
  border-color: var(--agate-accent-color, #8fd43a);
  border-radius: 0;
}
.popup:global(.carbon).top {
  border-radius: 0;
  max-width: inherit;
}
.popup:global(.carbon) .title {
  color: rgba(255, 255, 255, 0.75);
  padding: 0;
}
.popup:global(.carbon):not(.withTitle) .body {
  color: var(--agate-accent-color, #8fd43a);
  font-weight: normal;
}
.popup:global(.carbon).withTitle .body {
  padding: 24px;
  margin: -12px;
  border: 1px solid transparent;
  border-color: var(--agate-highlight-color, #6abe0b) transparent;
  background-color: hsla(var(--agate-highlight-h, 0), var(--agate-highlight-s, 0), var(--agate-highlight-l, 0), 0.4);
  background-image: none;
  border-radius: 0;
}
.popup:global(.carbon).withCloseButton .title {
  padding: 0 60px 0 0;
  padding-left: 0;
  padding-right: 60px;
}
:global(.enact-locale-right-to-left) .popup:global(.carbon).withCloseButton .title {
  padding-left: 60px;
  padding-right: 0;
}
.popup:global(.carbon) .buttons {
  padding: 0 0 0 12px;
  background-color: inherit;
  border-radius: inherit;
}
.popup:global(.carbon) .content {
  padding: initial;
}
.popup:global(.carbon) .closeButton {
  left: auto;
  right: 0;
  margin: 12px;
}
:global(.enact-locale-right-to-left) .popup:global(.carbon) .closeButton {
