.popupTransitionContainer {
  position: absolute;
  bottom: 0;
  height: 100%;
  width: 100%;
  pointer-events: none;
}
.popupTransitionContainer .inner {
  height: 100%;
}
.popupTransitionContainer .inner > * {
  pointer-events: auto;
