.timePicker .pickers {
  direction: ltr;
}
:global(.enact-locale-right-to-left) .timePicker .pickers {
  display: flex;
  align-items: center;
}
:global(.enact-locale-right-to-left) .timePicker .pickers .hourPicker {
  order: 2;
}
:global(.enact-locale-right-to-left) .timePicker .pickers .divider {
  order: 2;
}
:global(.enact-locale-right-to-left) .timePicker .pickers .minutePicker {
  order: 3;
}
:global(.enact-locale-right-to-left) .timePicker .pickers .meridiemPicker {
  order: 1;
}
.timePicker .pickers .hourPicker,
.timePicker .pickers .minutePicker,
.timePicker .pickers .meridiemPicker {
  position: relative;
}
.timePicker .pickers .hourPicker:not(:last-child)::after,
.timePicker .pickers .minutePicker:not(:last-child)::after,
.timePicker .pickers .meridiemPicker:not(:last-child)::after {
  content: '';
  width: 3px;
  height: 21px;
  opacity: 0.4;
  background-color: white;
  display: inline-block;
  vertical-align: inherit;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: calc(100% - 3px);
}
