.swatchButton:global(.carbon) {
  vertical-align: inherit;
}
.swatchButton:global(.carbon),
.swatchButton:global(.carbon).small {
  padding: 0;
}
.swatchButton:global(.carbon) .bg {
  border-radius: 999px;
  transform: none;
}
.swatchButton:global(.carbon) .colorSwatch {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transform: none;
  border-radius: 999px;
  margin: 6px;
}
