// Scrollbar.module.less
//

@import "../styles/skin.less";
@import "../styles/variables.less";

.scrollbar {
	background: inherit;
	display: flex;
	border-color: transparent;
	visibility: visible;
	opacity: 1;
	flex: none;

	transform: translateZ(0);
	-webkit-transform: translateZ(0);
	transition: opacity 0.1s linear;
	-moz-transition: opacity 0.1s linear;
	-webkit-transition: opacity 0.1s linear;

	&.vertical {
		flex-direction: column;
	}

	&.horizontal {
		:global(.enact-locale-right-to-left) & {
			// Flip the entire bar horizontally in RTL mode, and force it to remain in LTR.
			direction: ltr;
			transform: scaleX(-1);
		}
	}

	/* ScrollButton */
	.scrollbarButtonNoCompositeLayer {
		&,
		&:before,
		&:after,
		div,
		span {
			will-change: auto;
		}

		&:before {
			content: none;
		}

		div,
		span {
			-webkit-transform: initial;
			transform: initial;
		}
	}

	.scrollButton {
		.scrollbarButtonNoCompositeLayer;
		background-color: transparent;
		margin: 0;
	}

	/* ScrollbarTrack */
	.scrollbarTrackShown {
		opacity: 1;
	}
}

.applySkins({
	.scrollbar {
		::before {
			background-color: @agate-scroller-scrollbar-thumb-bg-color;
		}

		&.vertical {
			::before {
				width: @agate-scroller-scrollbar-thumb-width;
			}
		}
		&.horizontal {
			::before {
				height: @agate-scroller-scrollbar-thumb-width;
			}
		}

		&.corner {
			-webkit-padding-end: @agate-button-small-height;
		}

	}
});

@__DEV__: false;